/*=============================
  Base Styles
=============================*/
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
}
body {
  line-height: 1.6;
  background-color: #f5f5f5;
  color: #111;
  font-size: 16px;
  font-weight: 400;
  font-family: -apple-system, BlinkMacSystemFont, Avenir, "Avenir Next",
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
}
/*=============================
  Grid
=============================*/
.container {
  width: 80%;
  max-width: 960px;
  margin: 0 auto;
}
.row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
}
/* .row:last-child {
  margin-bottom: 0;
} */
.row .col {
  flex-grow: 1;
  flex-basis: 0;
}
.row .col,
.row [class^="col-"],
.row [class*=" col-"] {
  margin-right: 16px;
}
/* .row .col:last-child,
.row [class^="col-"]:last-child,
.row [class*=" col-"]:last-child {
  margin-right: 0;
} */
.row .col-1 {
  width: 8.333333333333332%;
}
.row .col-2 {
  width: 16.666666666666664%;
}
.row .col-3 {
  width: 25%;
}
.row .col-4 {
  width: 33.33333333333333%;
}
.row .col-5 {
  width: 41.66666666666667%;
}
.row .col-6 {
  width: 50%;
}
.row .col-7 {
  width: 58.333333333333336%;
}
.row .col-8 {
  width: 66.66666666666666%;
}
.row .col-9 {
  width: 75%;
}
.row .col-10 {
  width: 83.33333333333334%;
}
.row .col-11 {
  width: 91.66666666666666%;
}
.row .col-12 {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .row .col,
  .row [class^="col-"],
  .row [class*=" col-"] {
    flex-grow: 0;
    flex-basis: 100%;
  }
}
/*=============================
  Typography
=============================*/
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Avenir, "Avenir Next",
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 200;
}
h1 {
  font-size: 37.009px;
  line-height: 1.3;
}
h2 {
  font-size: 32.182px;
  line-height: 1.35;
}
h3 {
  font-size: 27.984px;
  line-height: 1.4;
}
h4 {
  font-size: 24.334px;
  line-height: 1.45;
}
h5 {
  font-size: 21.16px;
  line-height: 1.5;
}
h6 {
  font-size: 18.4px;
  line-height: 1.55;
}
p {
  font-size: 16px;
}
@media (max-width: 768px) {
  h1 {
    font-size: 33.311px;
  }
  h2 {
    font-size: 29.479px;
  }
  h3 {
    font-size: 26.088px;
  }
  h4 {
    font-size: 23.086px;
  }
  h5 {
    font-size: 20.43px;
  }
  h6 {
    font-size: 18.08px;
  }
}
/*=============================
  Form
=============================*/
input {
  height: 45px;
  width: 100%;
  margin-bottom: 16px;
  padding: 16px;
  font-size: 16px;
  background: #fff;
  /* border: 1px solid #a7a7a7; */
  border: 0;
  border-radius: 45px;
  transition: all 0.2s ease;
}
input:hover {
  border-color: #111;
}
input:focus {
  outline: none;
  border-color: #0062ff;
}
label {
  margin-right: 5px;
}
textarea {
  min-height: 70px;
}
/*=============================
  Button
=============================*/
button,
.button,
[type="submit"] {
  height: 45px;
  margin-bottom: 16px;
  padding: 10px 30px;
  outline: none;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 16px;
  background: #111;
  border: 0;
  border-radius: 45px;
  transition: all 0.2s ease;
}
button:hover,
.button:hover,
[type="submit"]:hover,
button:focus,
.button:focus,
[type="submit"]:focus {
  opacity: 0.9;
}
button:active,
.button:active,
[type="submit"]:active {
  opacity: 0.7;
}
button[disabled],
.button[disabled],
[type="submit"][disabled] {
  opacity: 0.8;
  cursor: not-allowed;
}
button.outline,
.button.outline,
[type="submit"].outline {
  border-width: 1.5px;
  color: #111;
  background: none;
}
button.outline:hover,
.button.outline:hover,
[type="submit"].outline:hover,
button.outline:focus,
.button.outline:focus,
[type="submit"].outline:focus {
  color: #f5f5f5;
  background: #111;
}

button[disabled]:hover {
  color: #f5f5f5;
  background: #111;
}

button.outline[disabled]:hover {
  color: #111;
  background: none;
}

/*=============================
  Link
=============================*/
a {
  color: #067bc2;
  transition: all 0.2s ease;
}
a:hover {
  cursor: pointer;
  color: #111;
}
/*=============================
  Image
=============================*/
img {
  margin-top: 0;
  margin-bottom: 16px;
}
/*=============================
  Code
=============================*/
pre {
  margin-top: 0;
  margin-bottom: 16px;
}
pre code {
  display: block;
  padding: 16px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
code {
  padding: 2px;
  white-space: nowrap;
  background: #e7e7e7;
  border: 1px solid #d7d7d7;
  border-radius: 2px;
  font-family: "Consolas", "Monaco", "Menlo", monospace;
}
